// api/active-user.js

import axios from 'axios'

const API_URL = 'http://jasdig.com:5154/apron/api'

export function url_login(userData) {
  return axios.post(`${API_URL}/login`, userData)
}
export function url_logout(userData) {
  return axios.post(`${API_URL}/logout`, userData)
}

//-----------------------------------------------------
export function url_find_info_jukir_by_range() {
  return axios.post('http://jasdig.com:5153/apron/api/find_info_jukir_by_range?stdate=now&endate=""')
}

export function url_detail_jukir(userData) {
  return axios.post('http://jasdig.com:5153/apron/api/find_info_jukir',null, {params:userData})
}
